<template>
  <v-row>
    <v-col class="py-0 px-4">
      <v-card elevation="0">
        <v-card-title>
          <v-row justify="space-between">
            <v-col cols="12" sm="4">
              Заявки
            </v-col>
            <v-col cols="12" sm="4">
              <v-select
                label="Категория" 
                :items="['All', 'Done', 'Process', 'Cancel']"
                v-model="group_type"
              ></v-select>
            </v-col>
            <v-col cols="12" sm="4">
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Поиск"
              ></v-text-field>
            </v-col>
          </v-row>
        </v-card-title>
      </v-card>
      <v-dialog 
        v-model="dialog" 
        max-width="540px"
      >
        <v-card>
          <v-card-title>
            {{ dialogTitle }}
            <v-spacer></v-spacer>
            <v-btn 
              color="error"
              icon
              :loading="loading"
              @click="checkWithdrawal()"
            >
              <v-icon>mdi-reload-alert</v-icon>
            </v-btn>
          </v-card-title>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn 
              color="blue darken-1" 
              text
              @click="cancelWithdrawal()"
            >
              Отмена
            </v-btn>
            <v-btn
              color="blue darken-1" 
              text
              @click="acceptWithdrawal()"
            >
              Подтверждение
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-data-table
        :headers="headers"
        :items="filteredItems"
        :search="search"
      >
        <template v-slot:item.player="props">
          <div 
            style="cursor: pointer"
            @click="$router.push({ name: 'users', params: { id: props.item.userId }})"
            >
            <v-avatar 
              size="35" 
              class="mr-2"
            >
              <img :src="props.item.src">
            </v-avatar>
            <span>{{ props.item.player }}</span>
          </div>
        </template>
        <template v-slot:item.action="{ item }">
          <v-btn 
            @click="openDialog(item, true)"
            icon
          >
            <v-icon small>
              mdi-check-circle-outline
            </v-icon>
          </v-btn>
          <v-btn 
            @click="openDialog(item)"
            icon
          >
            <v-icon small>
              mdi-close-circle-outline
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    dialogTitle: '',
    loading: false,
    group_type: null,
    search: '',
    headers: [
      { text: 'ID', align: 'left', value: 'userId' },
      { text: 'Дата', value: 'date' },
      { text: 'Игрок', value: 'player' },
      { text: 'Оператор', value: 'operator' },
      { text: 'Платежная система', value: 'paymentSystem' },
      { text: 'Кошелек', value: 'wallet' },
      { text: 'Сумма заявки', value: 'amount' },
      { text: 'Сумма вывода', value: 'withdrawal' },
      { text: 'Статус', value: 'status' },
      { text: 'Действия', value: 'action', sortable: false }
    ],
    items: [
      { 
        userId: 1,
        date: new Date().toISOString().substr(0, 10),
        player: 'Андрей',
        operator: 'MBANK',
        paymentSystem: 'y.ru',
        wallet: 'qiwi',
        amount: 150,
        withdrawal: 100,
        status: 'done',
        src: 'https://cdn.vuetifyjs.com/images/john.jpg'
      },
      { 
        userId: 2,
        date: new Date().toISOString().substr(0, 10),
        player: 'Саша',
        operator: 'MBANK',
        paymentSystem: 'x.ru',
        wallet: 'webmoney',
        amount: 250,
        withdrawal: 100,
        status: 'process',
        src: 'https://cdn.vuetifyjs.com/images/john.jpg'
      },
      { 
        userId: 3,
        date: new Date().toISOString().substr(0, 10),
        player: 'Ваня',
        operator: 'MBANK',
        paymentSystem: 'z.ru',
        wallet: 'qiwi',
        amount: 1000,
        withdrawal: 100,
        status: 'process',
        src: 'https://cdn.vuetifyjs.com/images/john.jpg'
      },
      { 
        userId: 4,
        date: new Date().toISOString().substr(0, 10),
        player: 'Катя',
        operator: 'MBANK',
        paymentSystem: 'x.ru',
        wallet: 'webmoney',
        amount: 200,
        withdrawal: 500,
        status: 'cancel',
        src: 'https://cdn.vuetifyjs.com/images/john.jpg'
      },
      { 
        userId: 5,
        date: new Date().toISOString().substr(0, 10),
        player: 'Артем',
        operator: 'MBANK',
        paymentSystem: 'y.ru',
        wallet: 'qiwi',
        amount: 600,
        withdrawal: 100,
        status: 'process',
        src: 'https://cdn.vuetifyjs.com/images/john.jpg'
      },
      { 
        userId: 6,
        date: new Date().toISOString().substr(0, 10),
        player: 'Даша',
        operator: 'MBANK',
        paymentSystem: 'z.ru',
        wallet: 'qiwi',
        amount: 400,
        withdrawal: 100,
        status: 'cancel',
        src: 'https://cdn.vuetifyjs.com/images/john.jpg'
      },
      { 
        userId: 7,
        date: new Date().toISOString().substr(0, 10),
        player: 'Иван',
        operator: 'MBANK',
        paymentSystem: 'x.ru',
        wallet: 'webmoney',
        amount: 1010,
        withdrawal: 1000,
        status: 'done',
        src: 'https://cdn.vuetifyjs.com/images/john.jpg'
      }
    ]
  }),
  computed: {
    filteredItems() {
      return this.items.filter((item) => {
        return (this.group_type == 'All') ? item : !this.group_type || (item.status.toLowerCase() === this.group_type.toLowerCase());
      });
    }
  },
  methods: {
    openDialog(user, accept) {
      this.dialogTitle = accept ? 'Подтверждение заявки на вывод средств' : 'Отклонение заявки на вывод средств';
      this.dialog = true;
    },
    checkWithdrawal() {
      this.loading = true;
      setTimeout(() => {
        this.dialog = false;
        this.loading = false;
      }, 1000)
    },
    cancelWithdrawal() {
      this.dialog = false;
    },
    acceptWithdrawal() {
      this.dialog = false;
    }
  }
}
</script>
